.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
  width: 100%;
}

.inputs {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.label {
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 600;
  color: rgb(3, 69, 107);
  font-size: 16px;
  margin: 0;
  padding: 0;
  line-height: 1;
  margin-bottom: 20px;
  position: relative;
}

.input {
  width: 100%;
  height: 24px;
  padding: 3px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.error {
  position: absolute;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 300;
  color: rgb(112, 112, 112);
  font-size: 13px;
}

.button {
  display: block;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
  min-width: 100px;
  height: 34px;
  cursor: pointer;
  box-sizing: content-box;
  text-align: center;
  line-height: 34px;
  border: 2px solid #fff;
  border-radius: 25px;
  background-color: rgb(3, 158, 62);
  padding: 0;
  will-change: transform;
  transition: transform 200ms linear;
}

.button:hover,
.button:focus {
  transition: transform 200ms linear;
  transform: scale(1.1);
  outline: none;
}
